.container {
    display: flex;
    flex-direction: column;
}

.label {
    margin-bottom: 5px;
}

.input {
    width: 200px;
    height: 30px;
    border: 1px solid rgba(33, 33, 33, 0.2);

    border-radius: 4px;

    font-size: 12px;
    line-height: 1.17;
    letter-spacing: 0.01em;


}

.input:focus-within {
    outline: none;
    border: 1px solid green;
}